import React from 'react';
import Contact from './Contact.js';

const ContactDisplay = ({ personsToShow, allPersons, setPersons }) => {
  return (
    <div className='contact_display'>
      <h1>Contacts</h1>
      {personsToShow.map(person => 
        <Contact key={person.firstName} person={person} setPersons={setPersons} allPersons={allPersons}></Contact>
      )}
    </div>
  );
}

export default ContactDisplay